const defaultState = {
  cityName: ""
}

export default (state = defaultState,action) => {
  // react-redux 
  //  state
  const newState=JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case "setCityName":// 
      newState.cityName=action.value;
      return newState
      break;
    case "changeCityName": // 
      newState.cityName=action.value;
      return newState
      break;
    default:
      break;
  }
  return state;
}